import React, { Component } from 'react'
import { Icon, Menu } from 'semantic-ui-react'
import { Link } from "react-router-dom";

export default class HeaderSui extends Component {
  state = { activeItem: '', menuVisible: false }
  
  handleItemClick = (e, { name }) => {
    this.setState({ activeItem: name, menuVisible: !this.state.menuVisible })
  }

  render() {
    const { activeItem } = this.state

    const MenuList = [
      {
        nameActive: 'Info&Skills',
        nameIcon: 'wrench',
        to: '/'
      }, 
      {
        nameActive: 'Projects',
        nameIcon: 'folder open',
        to: '/projects'
      }, 
      {
        nameActive: 'Contact',
        nameIcon: 'call',
        to: '/contact'
      }, 
      {
        nameActive: 'GitHub',
        nameIcon: 'github',
        to: ''
      },
    ]
    
  const MenuItems = MenuList.map((item) => {
    return (
      <Link 
       to={item.nameActive === 'GitHub' ? {pathname: 'https://github.com/VKMStudioX'}  : item.to }
       target={item.nameActive === 'GitHub' ? "_blank" : "_self"}
       key={item.nameActive}
      >
        <Menu.Item inverted 
          key={item.nameActive}
          name={item.nameActive}
          active={activeItem === item.nameActive}
          onClick={this.handleItemClick}
        >
          <Icon name={item.nameIcon} />
          {item.nameActive}
        </Menu.Item>
      </Link>
    );
  });

const MenuHeader = (
  <Menu icon='labeled' vertical stackable inverted className='header-menu' borderless  size='tiny'> 
{MenuItems}
      </Menu>
  )

  const MenuHeaderMobile = (
    <Menu icon='labeled'  inverted borderless size='big' className="header-menu header-menu-icon" >
         <Menu.Item inverted  vertical  name='Menu' open={this.state.menuVisible} onClick={() => this.setState({ menuVisible: !this.state.menuVisible })}>
          <Icon name='list ul' /></Menu.Item>
        </Menu>
    )


    return (
    <div className='sticky'>
{MenuHeaderMobile}
{this.state.menuVisible ? MenuHeader : null}
  </div>
    )
  }
}